@import "bourbon/bourbon";

.rtl {
	.mtphr-dnt {
		direction: ltr;
	}
	.mtphr-dnt-title {
		direction: rtl;
	}
	.mtphr-dnt-tick {
		direction: rtl;	
	}
}
.mtphr-dnt img {
  max-width: 100%;
  height: auto;
}
.mtphr-dnt-wrapper {
	position: relative;
	width: 100%;
}
.mtphr-dnt-inline-title {
	float: left;
	padding-right: 5px;
	.rtl & {
		float: right;
		padding-right: 0;
		padding-left: 5px;
	}
}
.mtphr-dnt-tick-container {
	position: relative;
	overflow: hidden;
}
.mtphr-dnt-tick-contents {
	position: relative;
	overflow: hidden;
}
.mtphr-dnt-tick {
	opacity: 0;
	margin: 0;
	img.emoji {
		max-width: none;
	}
}
.mtphr-dnt-list {
	.mtphr-dnt-tick {
		opacity: 1;	
	}
}
.mtphr-dnt-rotate,
.mtphr-dnt-scroll {
	.mtphr-dnt-tick {
		position: absolute;
		display:none;
	}
}
.mtphr-dnt-rotate {
	.mtphr-dnt-tick {
		opacity: 0;
	}
}
.mtphr-dnt-scroll-left,
.mtphr-dnt-scroll-right {
	.mtphr-dnt-tick {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		white-space: nowrap;
	}
}
.mtphr-dnt-trim-ticks .mtphr-dnt-tick {
	* {
		margin-top: 0 !important;
		margin-bottom: 0 !important;
		padding-top: 0 !important;
		padding-bottom: 0 !important;
	}
}

